.burger-btn {
  display: none;
  flex-direction: column;
  gap: 10px;
  cursor: pointer;
  z-index: 1111;
  padding: 3px;
}

.top-line, .mid-line, .bottom-line {
  width: 50px;
  height: 5px;
  background-color: #fff;
  border-radius: 20px;
  transition: all 400ms;
}

.burger-btn-close .top-line {
  transform: translate(0, 14px) rotate(-45deg);
}

.burger-btn-close .bottom-line {
  transform: translate(0, -15px) rotate(45deg);
}

.burger-btn-close .mid-line {
  opacity: 0;
}

/* Media Query: Max Width 701px  -------------------------------------*/

@media screen and (max-width: 701px) {
  .burger-btn {
    display: flex;
  }
}