.logo-wrapper {
  background-color: rgba(200, 27, 53, 0.8);
  color: #fff;
  border-radius: 10px;
  padding: .2em .5em;
  text-decoration: none;
}

h1 {
  font-size: 1.2rem;
  text-align: center;
}

/* Media Query: Max Width 1000px  -------------------------------------*/

@media screen and (max-width: 1000px) {
  h1 {
    font-size: 1em;
  }
}