.how-it-works {
  padding: 1em;
}


.how-it-works h2 {
  text-align: center;
  margin: 2rem 0;
}

.how-it-works .step {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3em 0;
  border-top: 1px solid black;
}

.how-it-works .step .step-img {
  width: 350px;
  height: 250px;
}

.how-it-works .step img {
  border-radius: 10px;
  width: 100%;
}

.how-it-works .step ul { 
  font-size: 1.5em;
  display: flex;
  flex-direction: column;
  gap: 1em;
} 

.how-it-works .step ul h3 {
  text-align: center;
}

.flex-reverse {
  flex-direction: row-reverse;
}

/* Media Query: Max Width 1000px  -------------------------------------*/

@media screen and (max-width: 1000px) {
  .how-it-works .step ul {
    font-size: 1em;
  }

  .how-it-works .step .step-img {
    width: 250px;
    height: 200px;
  }
}

/* Media Query: Max Width 701px  -------------------------------------*/
@media screen and (max-width: 701px) {
  .how-it-works .step ul {
    font-size: 1.5em;
    margin: 0 auto;
  }

  .how-it-works .step .step-img {
    display: none;
  }

  .flex-reverse {
    flex-direction: row;
  }
}