nav {
  padding: 1.5em;
}

nav ul {
  list-style: none;
  display: flex;
  gap: 1em;
  font-family: 'Fira Sans', sans-serif;
  width: 100%;
  justify-content: center;
}

nav ul li {
  text-align: center;
  min-width: 20px;
}

nav ul li a {
  text-decoration: none;
  color: #fff;
  padding: 1em;
  transition: opacity 300ms;
}

.login-btn {
  position: relative;
  right: -1rem;
  position: relative;
}

.login-btn::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 1px;
  top: 0;
  left: -14px;
  background-color: #fff;
}

/* Media Query: Max Width 1000px  -------------------------------------*/

@media screen and (max-width: 1000px) {
  nav ul {
    font-size: 0.75em;
    gap: .8em;
  }
}

/* Media Query: Max Width 701px  -------------------------------------*/


@media screen and (max-width: 701px) {
  nav {
    transform: translateY(-1000px);
    transition: transform 500ms;
    position: fixed;
    z-index: 111;
    inset: 0;
    height: 100%;
    justify-content: center;
    align-items: center;
    background-color: rgb(0 0 0 / 90%);
  }

  .nav-open {
    transform: translateY(0);
  }

  nav ul {
    height: 100%;
    justify-content: flex-start;
    padding-block: 2em;
    flex-direction: column;
    font-size: 1.5em;
    gap: 2em;
    margin-top: 5rem;
  }

  .login-btn {
    position: initial;
    border: none;
  }
}