.login-form { 
  background-color: #fff;
  padding: 1.5rem 3rem;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 1em;
  position: relative;
}

.login-form h2 {
  text-align: center;
  margin-bottom: 1rem;
}

.login-form .inputs-container {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.login-form .inputs-container input {
  width: 100%;
  font-size: 1em;
  padding: .7em;
  border-radius: 5px;
  border: 1px solid #000;
}

.remember-me {
  align-self: center;
  display: flex;
  align-items: center;
  gap: .3em;
}

.remember-me input {
  margin-top: .15em;
}

.login-form .submit-btn {
  font-size: 1.2em;
  background: #000;
  color: #fff;
  border-radius: 5px;
  padding: .4em;
  border: none;
  outline: none;
  cursor: pointer;
  
}

.login-form .submit-btn:hover {
  box-shadow: 0 3px 10px rgb(0 0 0 / 30%);
  transition: box-shadow 300ms;
}

.login-form .submit-btn:active {
  box-shadow: 0 1px 3px rgb(0 0 0 / 15%);
}

.login-form .close-btn {
  position: absolute;
  top: 1em;
  right: 1em;
  background: none;
  border: none;
  outline: none;
  font-size: 1.3em;
  cursor: pointer;
}

.login-form p {
  margin-top: 1em;
  text-align: center;
  font-size: .9em;
}

.login-form a {
  color: rgb(0, 89, 177);
  text-decoration: none;
  position: relative;
}

.login-form a:after {
  position: absolute;
  content: "";
  height: 1px;
  left: 50%;
  right: 50%;
  top: 110%;
  background-color: rgb(0, 89, 177);
  transition: all 500ms;
}

.login-form a:hover:after {
  left: 0;
  right: 0;
}

/* Media Query: Max Width 701px  -------------------------------------*/

@media screen and (max-width: 701px) {
  .login-form {
    position: absolute;
    inset: 0;
    padding: 1em;
    border-radius: 0;
  }

  .login-form h2 {
    font-size: 2em;
  }
}