footer {
  background-color: rgb(222, 222, 222);
  width: 100%;
  padding: .5rem;
  display: flex;
  gap: 2em;
  align-items: center;
  justify-content: space-between;
}

footer a {
  text-decoration: none;
  color: #000;
  position: relative;
}

footer li {
  list-style: none;
}

.privacy {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: .7em;
}

.social-media-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5em;
}

.social-media-container ul {
  display: flex;
  gap: .7em 1.2em;
}

.fa-brands {
  font-size: 1.7em;
  filter: contrast(10%);
  transition: all 450ms;
}

.fa-brands:hover {
  filter: contrast(100%);
}

.site-map {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: .7em;
}

.privacy a:after, .site-map a:after {
  content: "";
  height: 1px;
  background-color: #000;
  position: absolute;
  top: 110% ;
  left: 50%;
  right: 50%;
  transition: all 450ms;
}

.privacy a:hover:after, .site-map a:hover:after {
  right: 0;
  left: 0;
}

.amazon-logo {
  width: 150px;
  height: 100px;
  background: url('./aws-logo.png');
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}


/* Media Query: Max Width 830px  -------------------------------------*/

@media screen and (max-width: 830px) {
  footer {
    flex-direction: column;
  }

  .privacy {
    font-size: 1.2em;
  }

  .site-map a {
    font-size: 1.2em;
  }
}