.review-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 287px;
  gap: 0.5em;
  align-items: center;
  background-color: #f5f5f5;
  padding: 1em 0.5em 2em;
  border-radius: 10px;
  box-shadow: 0 1px 3px rgb(0 0 0 / 20%);
}

.img {
  border-radius: 50%;
}

.review-body {
  max-height: 50px;
  overflow-y: hidden;
  font-size: 0.85em;
  transition: all 1000ms;
}

.expand-review {
  max-height: 300px;
}

.review-wrapper button {
  color: #000;
  position: absolute;
  bottom: 0.5em;
  right: 50%;
  transform: translateX(50%);
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  font-family: 'Fira Sans', sans-serif;
  font-size: 0.8em;
}
