.reviews-section {
  margin-top: 1rem;
  padding: 1em 0;
  position: relative;
}

.reviews-container {
  overflow-x: hidden;
  padding: 0.5em;
}

.reviews {
  display: flex;
  flex-wrap: nowrap;
  flex-grow: 0;
  gap: 1em;
  align-items: flex-start;
  transition: all 500ms ease-in-out;
}

.review-slide-btn {
  position: absolute;
  top: 44%;
  border-radius: 50%;
  min-width: 30px;
  min-height: 30px;
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
  padding: .5em;
}

#review-slide-left {
  left: -2.5rem;
}

#review-slide-right {
  right: -2.5rem;
}

.review-slide-btn i {
  font-size: 1.2em;
}

.review-slide-btn:hover {
  background-color: rgb(0 0 0 / 5%);
}

.review-slide-btn:active {
  background-color: rgb(0 0 0 / 7%);
}